import React, { useState } from 'react';
import Form from 'react-bootstrap/Form';
import { useDispatch, useSelector } from 'react-redux';
import { addContact } from '../redux/contactSlice';
import { Button } from 'react-bootstrap';
import { selectContacts } from '../redux/selectors';

function Forms() {
  const dispatch = useDispatch();
  const contacts = useSelector(selectContacts);

  const [formData, setFormData] = useState({
    name: '',
    number: '',
    addToFavorites: false,
  });

  const nameRegex = /^[a-zA-Z--' -]+$/; //    
  const numberRegex = /^[0-9]+$/; //    

  const handleInputChange = e => {
    const { name, value, type, checked } = e.target;
    let inputValue = type === 'checkbox' ? checked : value;

    //     
    if (name === 'name' && !nameRegex.test(inputValue)) {
      inputValue = formData.name; //   ,     
    } else if (name === 'number' && !numberRegex.test(inputValue)) {
      inputValue = formData.number; //   ,     
    }

    setFormData(prevData => ({
      ...prevData,
      [name]: inputValue,
    }));
  };

  const handleSubmit = e => {
    e.preventDefault();

    // ,      '  
    const existingContact = contacts.find(
      contact =>
        contact.name.toLowerCase() === formData.name.toLowerCase() ||
        contact.number === formData.number
    );

    if (existingContact) {
      alert('   !');
      return; //   ,    
    }

    //       Redux Thunk
    dispatch(addContact(formData));

    //    
    setFormData({
      name: '',
      number: '',
      addToFavorites: false,
    });
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>Name</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter name"
          name="name"
          value={formData.name}
          onChange={handleInputChange}
        />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Number</Form.Label>
        <Form.Control
          type="text"
          placeholder="Number"
          name="number"
          value={formData.number}
          onChange={handleInputChange}
        />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicCheckbox">
        <Form.Check
          type="checkbox"
          label="Add to favorites"
          name="addToFavorites"
          checked={formData.addToFavorites}
          onChange={handleInputChange}
        />
      </Form.Group>

      <Button className="mb-3" variant="outline-primary" type="submit">
        Add contact
      </Button>

      {formData.addToFavorites}
    </Form>
  );
}

export default Forms;
